.post-item {
	position: relative;
	display: flex;
}

.post-item .img-box {
	width: 100px;
	height: 100px;
	object-fit: cover;
	border-radius: 50%;
	padding: 2px;
	border: 1px solid #dadada;
	flex: 0 0 100px;
}

.post-item .img-box img {
	width: 100%;
	border-radius: 50%;
}

.post-item header {
	display: flex;
}

.post-item p {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
}

.post-item header h1 {
	flex: 1 1;
	margin: 0;
	font-size: 22px;
	font-size: 20px;
	font-weight: 700;
	position: relative;
}

.post-item .content {
	padding-left: 15px;
	flex: 1 1;
}

.post-list {
	display: block;
	margin-bottom: 30px;
}

.post-list hr {
	width: 30%;
	height: 1px;
	margin: 0 auto;
	border: 0;
	background: #efefef;
}

.post-more {
	height: 30px;
	text-align: right;
}

.notice {
	padding: 20px;
	font-family: miranafont, 'Hiragino Sans GB', STXihei, 'Microsoft YaHei', SimSun, sans-serif;
	color: #505050;
	position: relative;
	margin-bottom: 60px;
	background: #fbfbfb;
	border-radius: 3px;
	width: 800px;
	margin: 0 auto 60px auto;
}

.top-feature {
	width: 800px;
	height: auto;
	margin: 0 auto;
	font-family: miranafont, 'Hiragino Sans GB', STXihei, 'Microsoft YaHei', SimSun, sans-serif;
}
.top-feature li {
	width: 32.9%;
	float: left;
	margin: 0 auto;
	position: relative;
	display: block;
}

.top-feature li:nth-child(2) {
	margin: 0 5px;
}

.top-feature img {
	height: 160px;
	width: 257px;
	object-fit: cover;
	border-radius: 5px;
}

h1.fes-title,
h1.main-title {
	color: #757575;
	font-size: 16px;
	font-weight: 500;
	padding-bottom: 15px;
	border-bottom: 1px dashed #ececec;
}

.feature-content {
	margin-top: 10px;
	display: inline-block;
	position: relative;
}

.feature-title span {
	position: absolute;
	width: 100%;
	height: 160px;
	text-align: center;
	line-height: 160px;
	background: #bbe2db;
	color: #fff;
	font-size: 16px;
	opacity: 0;
	border-radius: 5px;
}

.feature-title:hover span {
	position: absolute;
	width: 100%;
	height: 160px;
	text-align: center;
	line-height: 160px;
	background: #bbe2db;
	color: #fff;
	font-size: 16px;
	opacity: 1;
	border-radius: 5px;
}

.foverlay {
	transition: opacity 0.4s ease-out;
}

hr {
	width: 100%;
	height: 1px;
	border: 0;
	background: #efefef;
	margin: 15px 0;
}

ul > h2 {
	font-weight: 500;
	margin: 50px 0;
}

